* {
  margin: 0;
}

body {
  box-sizing: border-box;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  display: flex;
}

.stopwatch {
  --minute-duration: 60s;
  border: 1vmin dotted #000;
  border-radius: 100%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.stopwatch--speed-up {
  --minute-duration: 10s;
}

.stopwatch__minutes-hand {
  transform-origin: bottom;
  animation: rotate-hand calc(var(--minute-duration) * 60) steps(60) infinite;
  z-index: 1;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  position: absolute;
  bottom: 40vmin;
  left: 38.5vmin;
  transform: rotate(0);
}

.stopwatch__seconds-hand {
  transform-origin: bottom;
  animation: rotate-hand var(--minute-duration) linear infinite;
  z-index: 5;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  position: absolute;
  bottom: 40vmin;
  left: 39.25vmin;
  transform: rotate(0);
}

@keyframes rotate-hand {
  to {
    transform: rotate(360deg);
  }
}

.stopwatch__center {
  z-index: 10;
  background-color: #f6a603;
  border-radius: 100%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  bottom: 37.5vmin;
  left: 37.5vmin;
}

